import Vue from 'vue';
import Vuex from 'vuex';
import auth from './modules/auth';
import surveys from './modules/surveys';

Vue.use(Vuex);

const debug = process.env.NODE_ENV !== 'production';

const store = new Vuex.Store({
  modules: {
    auth,
    surveys,
  },
  strict: debug,
});

Vue.store = store;
export default store;
