<template>
  <div id="app">
    <topbar v-if="!$router.currentRoute.meta.dashboard" />
    <el-alert
      v-if="isConfirmationFailure"
      title="Код подтверждения недействителен!"
      type="warning"
      show-icon
      class="global-notice"
    />

    <el-alert
      v-if="isConfirmed"
      type="success"
      show-icon
      class="global-notice"
      title="Вы успешно подтвердили аккаунт!"
    />

    <router-view class="view" />
    <v-footer v-if="!$router.currentRoute.meta.dashboard" />
  </div>
</template>

<script>
import { mapState } from 'vuex';
import Topbar from './components/Topbar.vue';
import VFooter from './components/Footer.vue';

export default {
  metaInfo: {
    // title: 'Эффективная Команда',
    titleTemplate: '%s | Эффективная Команда',
  },

  components: { Topbar, VFooter },

  data() {
    return {
      isConfirmed: false,
      isConfirmationFailure: false,
    };
  },

  computed: {
    ...mapState('auth', ['current_user']),
  },

  mounted() {
    this.watchingRoute();
  },

  methods: {
    watchingRoute() {
      // В mounted & $nextTick маршрутизатор всё еще ссылается на корень / и не имеет данных.
      // Поэтому был использован наблюдатель изменения $route и его остановка после отработки.
      // Пример ссылки из письма для подтверждения регистрации (заменён хост):
      // http://localhost:8080/#/v1/confirmation?code=RPDuEx0dwDRVQQtxHglkpQ
      const stopWatchingRoute = this.$watch('$route', () => {
        stopWatchingRoute();
        this.verificationCode();
        // todo хэшик будет заменён на / ?
        if (this.$route.query.confirmation) {
          window.history.replaceState(null, null, '/#/');
        }
      });
    },

    verificationCode() {
      const { confirmation } = this.$route.query;

      switch (true) {
        // Приоритетнее!
        case confirmation === 'failure':
          this.isConfirmationFailure = true;
          break;

        case confirmation === 'success':
          this.isConfirmed = true;

          if (!this.current_user) {
            this.$router.push({ name: 'Login' });
          }

          break;

        default:
      }
    },
  },
};
</script>

<style lang="scss">
#app {
  height: 100%;

  .view {
    min-height: calc(100vh - 60px);
  }

  .global-notice {
    max-width: 1200px;
    position: absolute;
    left: 50%;
    top: calc(60px + 1rem);
    transform: translateX(-50%);
    z-index: 999;
  }
}
</style>
