/* eslint-disable camelcase */
// Простая аутентификация.
// В компонентах можно проверять: `if (current_user) {}`
// Далее в current_user будут лежать и политики доступа: `if (current_user.admin)`
import Vue from 'vue';

export default {
  namespaced: true,

  state: {
    current_user: null,
    token: null,
  },

  mutations: {
    authenticate(state, { user, token }) {
      state.current_user = user;
      state.token = token;
      Vue.axios.defaults.headers.common.Authorization = token;
    },
  },

  actions: {
    registration({ commit }, { token, user }) {
      localStorage.setItem('token', token);
      localStorage.setItem('current_user', JSON.stringify(user));
      commit('authenticate', { user, token });

      Vue.prototype.$message({
        type: 'success',
        duration: 10000,
        message: 'Вы успешно зарегистрировались! На Ваш адрес электронной почты выслана инструкция для завершения регистрации.',
      });
    },

    async login({ commit }, sign_in) {
      try {
        const response = await Vue.axios.post('/sign_in', { sign_in });
        const { user, token } = response.data;

        localStorage.setItem('token', token);
        localStorage.setItem('current_user', JSON.stringify(user));
        commit('authenticate', { user, token });

        // Действия после успешной аутентификации
        Vue.prototype.$message.success('Вы успешно вошли в систему');
        if (Vue.router.currentRoute.query.redirect) {
          Vue.router.push({ path: Vue.router.currentRoute.query.redirect });
        } else {
          Vue.router.push('/');
        }
      } catch (error) {
        Vue.prototype.$message.error('Логин или пароль введены неверно');
      }
    },

    logged({ commit }) {
      commit('authenticate', {
        token: localStorage.getItem('token'),
        user: JSON.parse(localStorage.getItem('current_user')),
      });
    },

    logout({ commit }) {
      localStorage.clear();
      commit('authenticate', {
        token: null,
        user: null,
      });
      Vue.router.push({ name: 'Home' });
    },
  },
};
