import Vue from 'vue';
import axios from 'axios';
import VueAxios from 'vue-axios';
import ElementUI from 'element-ui';
import locale from 'element-ui/lib/locale/lang/ru-RU';
import Meta from 'vue-meta';

import App from './App.vue';
import router from './router';
import store from './store';
import './styles';

// Use Date-fns in components, not globally, like this:
// import { format, formatDistance } from 'date-fns';
// import ru from 'date-fns/locale/ru';

Vue.use(VueAxios, axios);
Vue.use(ElementUI, { locale });
Vue.use(Meta);

// Настройка Axios
Vue.axios.defaults.baseURL = `${process.env.VUE_APP_API}/v1`;

// хук на Запрос к серверу
// Просто печатает реквест в консоль для дебага
Vue.axios.interceptors.request.use((request) => {
  console.info('Starting Request: ', request);
  return request;
}, err => Promise.reject(err));

// хук на Ответ от сервера
// Печатает ответ сервера в консоль
// А так же глобально обрабатывает ошибки с помощью Element UI Message

Vue.axios.interceptors.response.use((response) => {
  console.info('Getting Response: ', response);
  return response;
}, async (err) => {
  // eslint-disable-next-line no-prototype-builtins
  if (err.config.hasOwnProperty('localError') && err.config.localError) {
    return Promise.reject(err);
  }
  if (err.response) {
    console.log(err.response);
    switch (err.response.status) {
      case 401:
        Vue.prototype.$message.warning('Время сессии истекло');
        await store.dispatch('auth/logout');
        break;
      case 403:
        Vue.prototype.$message.error('Доступ к ресурсу запрещен');
        break;
      case 404:
        Vue.prototype.$message.error('Информация не найдена');
        break;
      case 405:
        Vue.prototype.$message.error('Действие недоступно');
        break;
      default:
        Vue.prototype.$message.error('Что-то пошло не так');
    }
  } else if (err.request) {
    Vue.prototype.$message.error('Нет соединения с сервером');
  } else {
    Vue.prototype.$message.error('Ошибка. Обновите страницу');
  }
  return Promise.reject(err);
});

// Проверка аутентификации при запуске приложения
const token = localStorage.getItem('token');
if (token) {
  store.dispatch('auth/logged');
}

Vue.config.productionTip = false;

new Vue({
  router,
  store,
  render: h => h(App),
}).$mount('#app');
