import Vue from 'vue';
import Router from 'vue-router';
import store from './store';

Vue.use(Router);

const router = new Router({
  // mode: 'history',
  // base: process.env.BASE_URL,
  base: process.env.NODE_ENV === 'production'
    ? '/efco/'
    : '/',
  routes: [
    {
      path: '/',
      alias: '/confirmation',
      name: 'Home',
      component: () => import('./views/Home.vue'),
    },
    {
      path: '/login',
      name: 'Login',
      component: () => import('./views/Login.vue'),
    },
    {
      path: '/registration',
      name: 'Registration',
      component: () => import('./views/Registration.vue'),
    },
    {
      path: '/about',
      name: 'About',
      meta: { auth: true },
      component: () => import('./views/About.vue'),
    },
    {
      path: '/dashboard',
      name: 'Dashboard',
      redirect: { name: 'DashboardSurveys' },
      meta: { auth: true, dashboard: true },
      component: () => import('./views/admin/Dashboard.vue'),
      children: [
        {
          path: 'surveys',
          name: 'DashboardSurveys',
          meta: { auth: true, dashboard: true },
          component: () => import('./views/admin/DashboardSurveys.vue'),
        },
        {
          path: 'surveys/new',
          name: 'DashboardSurveysNew',
          meta: { auth: true, dashboard: true },
          component: () => import('./views/admin/DashboardSurveysNew.vue'),
        },
        {
          path: 'surveys/:id',
          props: true,
          name: 'DashboardSurveysShow',
          meta: { auth: true, dashboard: true },
          component: () => import('./views/admin/DashboardSurveysShow.vue'),
        },
        {
          path: 'surveys/:id/statistics',
          props: true,
          name: 'DashboardSurveysStatistics',
          meta: { auth: true, dashboard: true },
          component: () => import('./views/admin/DashboardSurveysStatistics.vue'),
        },
        {
          path: 'appeals',
          name: 'DashboardAppeals',
          meta: { auth: true, dashboard: true },
          component: () => import('./views/admin/DashboardAppeals.vue'),
        }, {
          path: 'users',
          name: 'DashboardUsers',
          meta: { auth: true, dashboard: true },
          component: () => import('./views/admin/DashboardUsers.vue'),
        },
        {
          path: 'users/:id',
          props: true,
          name: 'DashboardUsersShow',
          meta: { auth: true, dashboard: true },
          component: () => import('./views/admin/DashboardUsersShow.vue'),
        },
      ],
    },
    {
      path: '/surveys',
      name: 'Surveys',
      component: () => import('./views/Surveys.vue'),
    },
    {
      path: '/surveys/:id',
      name: 'SurveyPassing',
      props: true,
      component: () => import('./views/SurveyPassing.vue'),
    },
    {
      path: '/account',
      name: 'Account',
      meta: { auth: true },
      component: () => import('./views/Account.vue'),
    },
    {
      path: '/appeals',
      name: 'Appeals',
      component: () => import('./views/Appeals.vue'),
    },
    // Редирект на главную страницу при запросе несуществующих роутов
    // Всегда должен быть последним, в конце массива!
    {
      path: '*',
      redirect: '/',
    },
  ],
});

// Хук Роутера: проверяет аутентификацию и редиректит на форму логина и обратно
router.beforeEach((to, from, next) => {
  if (to.matched.some(record => record.meta.auth)) {
    if (store.state.auth.current_user) {
      next();
    } else {
      next({ name: 'Login', query: { redirect: to.path } });
      Vue.prototype.$message.warning('Войдите в систему');
    }
  } else if (to.name === 'Login' && store.state.auth.current_user) {
    next(false);
  } else {
    next();
  }
});


Vue.router = router;
export default router;
